.box-layout {
    width    : 340px;
    margin   : 50px auto;
    font-size: 15px;

    form {
        margin-bottom: 15px;
        background   : #f7f7f7;
        box-shadow   : 0px 2px 2px rgba(0, 0, 0, 0.3);
        padding      : 30px;
    }

    h2 {
        margin: 0 0 15px
    }
}

a.link-tag {
    color: white;

    &:hover {
        color          : white;
        text-decoration: none;
    }
}